<template>
  <div id='app'>
    <header class='kakao_header'>
      <div class='header_top'>
        <!-- 상단바 -->
        <div class='header_top_column'>
          <img src='./assets/cellular.png' alt='cellular'>
          <img src='./assets/wifi.png' alt='wifi'>
        </div>
        <div class='header_top_column'>
          <span class='header_time'>11:02 AM</span>
        </div>
        <div class='header_top_column'>
          <span class='header_battery'>
            100%
            <img src='./assets/battery.png' alt='battery'>
          </span>
        </div>
      </div>
      <div class='header_bottom'>
        <!-- 상단메뉴 -->
        <div class='header_bottom_column header_bottom_icon'>
          <span class='header_bottom_title'>{{title}}</span>
          <router-link to='/friends'>
            <img :src='icon'>
          </router-link>
        </div>
        <div class='header_bottom_column'>
          <span class='header_bottom_subject'>{{subject}}</span>
        </div>
        <div class='header_bottom_column'>
          <img :src='img1'>
          <img :src='img2'>
          <img :src='img3'>
          <img :src='img4'>
        </div>
      </div>
    </header>

    <router-view></router-view>

    <nav class='tab_bar'>
      <router-link to='/friends' class='tab_icon'>
        <span class='tab_icon1'></span>
      </router-link>
      <router-link to='/chats' class='tab_icon'>
        <span class='tab_icon2'></span>
      </router-link>
      <router-link to='/find' class='tab_icon'>
        <span class='tab_icon3'></span>
      </router-link>
      <router-link to='/more' class='tab_icon'>
        <span class='tab_icon4'></span>
      </router-link>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      title: '',
      icon: '',
      subject: '',
      img1: '',
      img2: '',
      img3: '',
      img4: ''
    }
  },
  destroy () {
    this.$off('changeData')
  },
  created () {
    const _self = this

    this.$on('changeData', function (
      newTitle,
      newIcon,
      newSubject,
      newImg1,
      newImg2,
      newImg3,
      newImg4
    ) {
      _self.title = newTitle
      _self.icon = newIcon
      _self.subject = newSubject
      _self.img1 = newImg1
      _self.img2 = newImg2
      _self.img3 = newImg3
      _self.img4 = newImg4
    })
  },
  components: {},
  methods: {}
}
</script>

<style lang='scss' src='./styles/main.scss'></style>
